}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#27282f;overflow-x:hidden}

.App{font-family:'Montserrat', sans-serif;text-align:center;color:#000;background-color:#e9e7e7;min-height:100vh;position:relative;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin:auto}.App-header,.App-footer{background:linear-gradient(to right, #b1ebf5, #67def3);color:#e9e7e7;width:100%}.App-header{min-height:20vh;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center;box-shadow:0vh 5.5vh 6vh -7vh #000}.App-footer{min-height:5vh;position:absolute;bottom:0;box-shadow:0vh 5.5vh 6vh 2.5vh #000}h1{width:100%;margin:auto;letter-spacing:.25em}.App-footer [alt^="pixabay.com"]{width:150px}svg path{fill:white}@media (max-width: 768px){h1{font-size:2em}h2{font-size:1.5em}.App-footer [alt^="pixabay.com"]{width:100px}}@media (max-width: 420px){h1{font-size:1.5em}h2{font-size:1em}.App-header{min-height:15vh}}

.Todo-card{height:5vw;display:-ms-flexbox;display:flex;-ms-flex-pack:space-evenly;justify-content:space-evenly;box-shadow:0px 6px 13px 1px #cfcccc;margin-top:10%;color:#277883}.Todo-card div{float:left;clear:both;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center}.Todo-card div:nth-child(1){width:10%;padding-left:5%}.Todo-card div:nth-child(2){width:70%}.Todo-card div:nth-child(3){width:10%}.Todo-card p{font-size:1.25em;line-height:3em}.Todo-card span{font-family:'Frijole', cursive;font-family:'Acme', sans-serif;font-size:2em;line-height:2em;padding-right:5%}.Todo-card span:hover{cursor:pointer;color:red}.Todo-card img{width:100%;height:90%;border-radius:100%}@media (max-width: 1650px){.Todo-card{height:7vw;margin-top:5%}}@media (max-width: 1250px){.Todo-card{height:10vw;margin-top:3%}}@media (max-width: 768px){.Todo-card{height:18vw}.Todo-card p{font-size:1em;line-height:5em}.Todo-card span{font-size:1em;line-height:6em}.Todo-card div:nth-child(1){width:20%;padding-left:5%}}@media (max-width: 568px){.Todo-card{height:18vw}.Todo-card p{font-size:.75em;line-height:5em}.Todo-card span{font-size:.75em;line-height:6em}.Todo-card div:nth-child(1){width:20%;padding-left:5%}}@media (max-width: 420px){.Todo-card{height:25vw}.Todo-card p{font-size:3vw}.Todo-card span{font-size:3vw}.Todo-card div:nth-child(1){padding-left:2%}.Todo-card img{height:70%;margin-top:20%}}

.Tooltip{width:100%;background-color:black;color:#fff;text-align:center;padding:5px 0;position:absolute;left:0;top:0;margin:auto;z-index:1;font-size:8pt}.visible{visibility:visible}.invisible{visibility:hidden}button:hover{cursor:pointer}

.SubmitButton{background-color:transparent;border:solid 1px #67def3;color:#277883;padding:10px 50px;text-align:center;font-size:1em;letter-spacing:.2em;height:100%}@media (max-width: 850px) and (orientation: landscape){.SubmitButton{width:100%;padding:15px 50px}}@media (max-width: 420px){.SubmitButton{width:100%;font-size:.75em}}


.List{width:50%;list-style-type:none;margin:auto;margin-top:50px;margin-bottom:100px}.Input input{text-align:center;width:50%;padding:11px 0px;color:#277883;border:none;background-color:#e9e7e7;background-image:url(data:image/gif;base64,);background-position:5px 0px;background-repeat:no-repeat;background-size:contain;font-size:18px}.Input input:focus{outline:none}.Forget{margin-top:5vw;margin-bottom:10vw}@media (max-width: 1650px){.List{width:80%}}@media (max-width: 1250px){.Input input{width:80%;margin-bottom:2%}}@media (max-width: 768px){.Input input{width:100%}}@media (max-width: 568px){.List{width:96%}.Forget{margin-top:10vw;margin-bottom:20vw}}@media (max-width: 850px) and (orientation: landscape){.List{width:96%}}

